
.edit-textarea {
		-ms-flex: 1;
		    flex: 1;
		border-right: 1px solid #ccc;
		padding: 20px
}
.preview {
		-ms-flex: 1;
		    flex: 1;
		padding: 20px;
}
input,
	textarea {
		width: 100%;
		padding: 5px;
		margin-bottom: 10px;
}
div[contenteditable="true"] {
		border: 1px solid #ccc;
		padding: 5px;
}
.detail-content {
		min-height: 50vh
}
.edit-textarea p .tag-wrap {
		padding-left: 20px;
}
.edit-textarea label.tag {
		position: relative;
		margin-right: 15px;
        border:1px solid #00aaff;
        border-radius:5px;
        padding: 5px 7px;
        cursor: pointer;
}
.edit-textarea label.tag:after {
		content: 'X';
		position: absolute;
		right: -8px;
		top: 0;
		color: #f44336;
		font-size: 12px;
		visibility: hidden;
}
.edit-textarea label.tag:hover:after {
		visibility: visible;
}



